@import "~@blueprintjs/core/lib/css/blueprint.css";
@import "~@blueprintjs/core/lib/scss/variables";

.available-rooms {
  width: 50vw;
  text-align: center;

  .table-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-height: 70vh;
    overflow-y: scroll;
  }

  span.space {
    width: 10px;
    display: inline-block;
  }

  > p {
    margin-bottom: 30px;
  }
}
